/*公共样式*/
body{
  background: #fff;
  padding: 15px;
}

input{
  margin: 0!important;
  font-size: 14px;
  border:none;
}

input::-webkit-input-placeholder {
  /* placeholder颜色  */
    color: #ccc;
  /* placeholder字体大小  */
    font-size: 14px;
  /* placeholder位置  */
}

textarea::-webkit-input-placeholder{
  /* placeholder颜色  */
  color: #ccc;
  /* placeholder字体大小  */
  font-size: 14px;
  /* placeholder位置  */
}

*,p,div,span,a,input,select{
  padding: 0;
  margin: 0;
}
li{
  list-style: none;
}

//弹出信息toast样式
.mint-toast {
  max-width: 100% !important;
}

//弹出信息toast样式
.mint-toast.is-placemiddle {
  top: 20px!important;
  width: 100% !important;
  background: #eac076;
  border-radius: 0 !important;
  &.bg{
    background: rgba(3,3,3,0.6)!important;
  }

}

//底部pop样式替换
.mint-popup-bottom {
  width: 100%!important;
  height: 100vh!important;
  overflow: hidden;
  box-sizing: border-box!important;
  .close{
    width: 100%;
    height: 44px;
    position: fixed;
    left: 0;
    top:0;
    z-index: 999;
    background: #fff;
    img{
      width: 15px;
      height: 15px;
      position: absolute;
      right: 15px;
      top: 15px;
    }
  }
}

.mint-indexlist-nav{
  border:none!important;
  background: none!important;
}

//城市，品牌列表
.mint-indexsection{
  .mint-cell-wrapper{
    padding: 0 20px 0 15px!important;
  }
}

.mint-indexsection-index{
  background-color:#ebf0f4!important;
}
.mint-toast {
  max-width: 100% !important;
}

//多选择框样式更改
.mint-checkbox-core{
  border-radius: 0!important;
  width: 14px!important;
  height: 14px!important;
}
.mint-checkbox-core{
  background-color: #fff!important;
  border-color: #bebebe!important;
}
.mint-checkbox-input:checked + .mint-checkbox-core::after{
  border-color: #333!important;
}
.mint-checkbox-core::after{
  top: calc(~'50% - 5px')!important;
  left: calc(~'50% - 3px')!important;
}

.avatar-uploader .el-upload {
  border-radius: 0;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  text-align: center;
}
.avatar {
  width: 100%;
  display: block;
  &.avatar{
    width: 100%!important;
  }
}

.avatar-uploader{
    width: 30%;
}

//select最少宽度
select{
  min-width: 40%;
  min-height: 30px;
}

//弹出confirm样式
.mint-msgbox{
  border-radius: 10px!important;
  width: 250px!important;
  .mint-msgbox-cancel{
    color: #006aff;
  }
  .mint-msgbox-confirm{
    color: #006aff;
  }
  .mint-msgbox-message{
    color: #333!important;
    font-size: 12px!important;
  }
}

//成功的toast样式
.bg{
  background: rgba(3,3,3,0.6)!important;
}



